import { reactive } from "vue";
import { v4 as uuidv4 } from "uuid";
import { storage } from "../utils/storage";
import { githubRepo } from "../utils/git";
import { titles } from "./Titles";

export interface Evento {
  id: string;
  list: string;
  title: string;
  locale: string;
  desc: string;
  date?: Date;
  end?: Date;
  gid?: string;
  updated?: Date;
  recurring?: boolean;
}

export interface Repository {
  selected: Evento | null;
  all(): Evento[];
  find(id: string, date: Date): Evento | undefined;
  save(source: Evento): Evento;
  clear(): void;
}

class MemoryRepository implements Repository {
  private events: Evento[] = [];
  selected: Evento | null = null;

  all() {
    return this.events;
  }

  find(id: string, date: Date) {
    return this.events.find((e) => e.id === id && e.date === date);
  }

  save(source: Evento) {
    if (!source.id) source.id = uuidv4();
    source.title = source.title.toUpperCase();
    if (this.selected && this.selected.id && this.selected.date) {
      const evento = this.find(this.selected.id, this.selected.date);
      if (evento) {
        const isUpdate =
          (!evento?.updated && source.updated) ||
          (evento.updated && source.updated && evento.updated < source.updated);
        if (isUpdate) Object.assign(evento, source);
        this.sort();
        return evento;
      }
    }
    this.events.push(source);
    this.sort();
    return source;
  }

  sort() {
    this.events.sort((a, b) => {
      const titleA = titles.indexOf(a.title.toUpperCase());
      const titleB = titles.indexOf(b.title.toUpperCase());

      if (titleA !== titleB) {
        return titleA - titleB;
      }

      if (b.date && a.date && b.date.getTime() !== a.date.getTime()) {
        return a.date.getTime() - b.date.getTime();
      }

      if (a.locale !== b.locale) {
        return a.locale.localeCompare(b.locale);
      }

      return 0;
    });
  }

  clear() {
    this.events = [];
  }
}

class EventApplication {
  constructor(private readonly repo: Repository = new MemoryRepository()) {}

  get selected(): Evento | null {
    return this.repo.selected;
  }

  set selected(event: Evento | null) {
    this.repo.selected = event;
  }

  save(source: Evento) {
    try {
      if (
        this.selected &&
        this.selected.date &&
        this.selected.date?.toISOString() !== source.date?.toISOString()
      ) {
        this.selected.desc = "REMOVE";
        this.selected = null;
        return this.repo.save({ ...source, id: "" });
      } else {
        return this.repo.save(source);
      }
    } catch (error) {
      console.log(error);
    }
  }

  all(): Evento[] {
    return this.repo.all();
  }

  json() {
    return JSON.stringify(this.all());
  }

  remove(event: Evento) {
    event.desc = "REMOVE";
    return this.repo.save(event);
  }

  find(event: Evento) {
    if (event.date) return this.repo.find(event.id, event.date);
  }

  reload() {
    console.log("Recarregar eventos do servidor!!!");
  }

  async init() {
    const cache = storage.getItem("events");
    if (cache && cache.length) {
      // eslint-disable-next-line @typescript-eslint/no-explicit-any
      cache.forEach((e: any) => {
        this.save(this.prepareEvent(e));
      });
    }
    this.loadEventData().then((data) => {
      if (data && data.length) {
        this.repo.clear();
        data.forEach((e: Evento) => {
          this.save(this.prepareEvent(e));
        });
        this.storageSave();
      }
    });
  }

  storageSave() {
    storage.saveItem("events", this.all());
  }

  async gitSave() {
    const eventsNew = this.all();
    const eventsOld = await this.loadEventData();
    eventsNew.forEach((eventNew) => {
      const old = eventsOld.find(
        (old) =>
          old.id === eventNew.id &&
          old.updated &&
          eventNew.updated &&
          old.updated > eventNew.updated
      );
      if (old) Object.assign(eventNew, old);
    });
    try {
      this.storageSave();
      githubRepo.commitFile("data/events.json", JSON.stringify(this.all()));
    } catch (error) {
      alert("Erro ao salver item!!!");
    }
  }

  async loadEventData() {
    let data: Evento[] = [];
    try {
      const admin = localStorage.getItem("key");
      if (admin) {
        const res = await githubRepo.getContentData("data/events.json");
        if (res?.content) data = res.content;
      } else {
        const response = await fetch(
          `/data/events.json?v=${new Date().toISOString().replace(/\D/g, "")}`,
          {
            method: "GET",
            headers: {
              "Content-Type": "application/json",
            },
          }
        );
        if (response.ok) {
          data = await response.json();
        }
      }
    } catch (error) {
      console.log("Erro ao carregar dados:", error);
    }
    return data;
  }

  prepareEvent(eventData: Partial<Evento>): Evento {
    return {
      id: eventData.id || uuidv4(),
      list: eventData.list || "",
      title: eventData.title || "",
      locale: eventData.locale || "",
      desc: eventData.desc || "",
      date: eventData.date ? new Date(eventData.date) : undefined,
      end: eventData.end ? new Date(eventData.end) : undefined,
      updated: eventData.updated ? new Date(eventData.updated) : undefined,
      gid: eventData.gid,
      recurring: eventData.recurring,
    };
  }
}

export const eventApp = reactive(new EventApplication());

eventApp.init();
